import React, { useState, useEffect } from 'react';
import './CharacterToDo.css';
import AddCharacter from '../Utils/AddCharacter';

import {
  Segment,
  Grid,
  Container,
  Icon,
  Dimmer,
  Loader,
  Image,
} from 'semantic-ui-react';
import RestValueMobile from '../GridItem/RestValueMobile';
import CharacterAvatar from './CharacterAvatar';
import PerIdNote from './PerIdNote';
import {
  ChaosDunValue,
  GuardianDunValue,
  EponaValue,
  WeeklyGuardian,
} from '../GridItem/DungeonAndEpona';
import {
  AbyssDun2,
  ArgosRaid,
  BaltanRaid,
  BiakissRaid,
  KukseitnRaid,
  AbrelRaid,
} from '../GridItem/AbyssAndRaid';
import axios from 'axios';
import cookie from 'js-cookie';

import { ToastContainer } from 'react-toastify';
import { useHistory } from 'react-router-dom';

import { backendUrl, axiosConfigAuth } from '../Utils/ConstVar';
import {
  viewDataMain,
  applyChangesUtil,
  alarmRestValueUtil,
  toastMessage,
} from '../Utils/ViewDataUtil';
import AddAndChange from '../HomeSubComp/AddAndChange';
import PaginationComp from '../HomeSubComp/PaginationComp';
import SettingChange from '../HomeSubComp/SettingChange';
import AlarmAndNoteMobile from '../HomeSubComp/AlarmAndNoteMobile';

function CharacterToDoRowMobile({ limit, type }) {
  const todayDate = new Date(Date.now());

  const [userTodoData, setUserTodoData] = useState([]);

  const [addCharacterModal, setAddCharacterModal] = useState(false);

  const [loading, setLoading] = useState(false);

  const [activePage, setActivePage] = useState(1);

  const [pagination, setPagination] = useState(0);

  const [viewByCheckBox, setViewByCheckBox] = useState(false);

  const history = useHistory();

  const addCharacter = () => {
    setAddCharacterModal(true);
  };

  const closeAddCharacter = () => {
    setAddCharacterModal(false);
  };

  const viewPage = async (minusOne = false, plusOne = false, plusPage = 0) => {
    setLoading(true);
    setUserTodoData([]);

    const resultData = await viewDataMain(
      minusOne,
      plusOne,
      plusPage,
      limit,
      activePage,
      setActivePage,
      cookie.get('loadoUserToken')
    );

    if (resultData.success) {
      setUserTodoData(resultData.viewData.data);
      setPagination(resultData.setPage);
    } else {
      toastMessage('  ', 'error');
    }
    setLoading(false);
  };

  const applyChanges = async () => {
    setLoading(true);

    let submitData = userTodoData.filter(
      (item) => item.attributeChanged === true
    );

    const applyResult = await applyChangesUtil(
      submitData,
      cookie.get('loadoUserToken')
    );

    setLoading(false);
    if (applyResult) {
      toastMessage('    ', 'error');
    }
    viewPage();
  };

  const pageChange = async (event, data) => {
    setLoading(true);
    setActivePage(data.activePage);

    //   2    1     
    //       ID          
    setUserTodoData([]);

    await axios
      .get(
        `${backendUrl}/loado/api/homeworks?limit=${limit}&page=${data.activePage}`,
        axiosConfigAuth(cookie.get('loadoUserToken'))
      )
      .then((response) => {
        setUserTodoData(response.data.data);
        setLoading(false);
      })
      .catch((err) => {
        toastMessage('  ', 'error');
        setLoading(false);
      });
  };

  // for alarm animation
  const [alarmTrue, setAlarmTrue] = useState(true);

  // for note per list
  const [showNote, setShowNote] = useState(false);

  const alarmRestValue = (todoList) => {
    const alarmDataResult = alarmRestValueUtil(todoList, alarmTrue);
    setUserTodoData(alarmDataResult.alarmList);
    setAlarmTrue(alarmDataResult.alarm);
  };

  // if no user cookie then redirect to login page
  useEffect(() => {
    let loginCookie = cookie.get('loadoUserToken');
    !loginCookie && history.push('/login');
  }, []);

  useEffect(() => {
    viewPage();
  }, []);

  return (
    <>
      {loading ? (
        <Segment
          className='fullPage'
          style={{ height: '94vh', border: 'none' }}
        >
          <Dimmer active>
            <Loader size='big'></Loader>
          </Dimmer>
        </Segment>
      ) : (
        <Segment
          style={{
            width: '100%',
            margin: '0px',
            textAlign: 'center',
            paddingTop: '0px',
            backgroundColor: '#384862',
            height: '100%',
            padding: '0px',
          }}
        >
          <PaginationComp
            pagination={pagination}
            activePage={activePage}
            pageChange={pageChange}
          />
          <div
            style={{
              display: 'flex',
              marginTop: '20px',
              justifyContent: 'space-between',
              marginBottom: '10px',
            }}
          >
            <SettingChange
              viewByCheckBox={viewByCheckBox}
              setViewByCheckBox={setViewByCheckBox}
              deviceType='mobile'
            />
            <AddAndChange
              addCharacter={addCharacter}
              applyChanges={applyChanges}
              style={{ fontSize: '5px' }}
            />
          </div>
          <Segment
            basic
            style={{
              backgroundColor: 'dimgray',
              marginTop: '0px',
              fontSize: '10px',
              color: 'white',
            }}
          >
            <Grid columns={limit + 1}>
              <Grid.Row
                style={{
                  borderBottom: !showNote && '0.05rem inset ivory',
                }}
              >
                <Grid.Column className='contentColumn'>
                  <AlarmAndNoteMobile
                    alarmTrue={alarmTrue}
                    alarmRestValue={alarmRestValue}
                    userTodoData={userTodoData}
                    showNote={showNote}
                    setShowNote={setShowNote}
                  />
                </Grid.Column>
                {userTodoData.map((item, idx) => (
                  <CharacterAvatar
                    itemId={item._id}
                    character={item.character}
                    characterName={item.characterName}
                    attributeChanged={item.attributeChanged}
                    weeklyAttributeChanged={item.weeklyAttributeChanged}
                    axiosConfigAuth={axiosConfigAuth}
                    viewPage={viewPage}
                    alarmCharacter={item.alarmCharacter}
                    limit={limit}
                    dontChange={item.dontChange}
                    userTodoData={userTodoData}
                    setUserTodoData={setUserTodoData}
                    deviceType='mobile'
                  />
                ))}
              </Grid.Row>
              <Grid.Row className='eachRow'>
                <Grid.Column className='contentColumn'>
                  <Icon
                    name='calendar check outline'
                    style={{ fontSize: '24px' }}
                  />
                </Grid.Column>
                {userTodoData.map((item, idx) => (
                  <RestValueMobile
                    item={item}
                    userTodoData={userTodoData}
                    setUserTodoData={setUserTodoData}
                  />
                ))}
              </Grid.Row>
              <Grid.Row className='eachRow'>
                <Grid.Column className='contentColumn'>
                  <div>
                    <Image
                      src='./images/loa_icons/chaosDun.png'
                      avatar
                      className='contentImage'
                    />
                  </div>
                </Grid.Column>
                {userTodoData.map((item, idx) => (
                  <ChaosDunValue
                    chaosItem={item}
                    userTodoData={userTodoData}
                    setUserTodoData={setUserTodoData}
                    viewByCheckBox={viewByCheckBox}
                  />
                ))}
              </Grid.Row>
              <Grid.Row className='eachRow'>
                <Grid.Column className='contentColumn'>
                  <div>
                    <Image
                      src='./images/loa_icons/guardianDun.png'
                      avatar
                      className='contentImage'
                    />
                  </div>
                </Grid.Column>
                {userTodoData.map((item, idx) => (
                  <GuardianDunValue
                    guardianItem={item}
                    userTodoData={userTodoData}
                    setUserTodoData={setUserTodoData}
                    viewByCheckBox={viewByCheckBox}
                  />
                ))}
              </Grid.Row>
              <Grid.Row className='eachRow'>
                <Grid.Column className='contentColumn'>
                  <div>
                    <Image
                      src='./images/loa_icons/epona.png'
                      avatar
                      className='contentImage'
                    />
                  </div>
                </Grid.Column>
                {userTodoData.map((item, idx) => (
                  <EponaValue
                    eponaItem={item}
                    userTodoData={userTodoData}
                    setUserTodoData={setUserTodoData}
                    viewByCheckBox={viewByCheckBox}
                  />
                ))}
              </Grid.Row>
              <Grid.Row className='eachRow'>
                <Grid.Column className='contentColumn'>
                  <div>
                    <Image
                      src='./images/loa_icons/guardianDun.png'
                      avatar
                      className='contentImage'
                    />
                  </div>
                </Grid.Column>
                {userTodoData.map((item, idx) => (
                  <WeeklyGuardian
                    weeklyGuardianItem={item}
                    userTodoData={userTodoData}
                    setUserTodoData={setUserTodoData}
                    viewByCheckBox={viewByCheckBox}
                  />
                ))}
              </Grid.Row>
              <Grid.Row className='eachRow'>
                <Grid.Column className='contentColumn'>
                  <div>
                    <Image
                      src='./images/loa_icons/abyss2types.png'
                      avatar
                      className='contentImage'
                    />
                  </div>
                </Grid.Column>
                {userTodoData.map((item, idx) => (
                  <AbyssDun2
                    abyssDun2Item={item}
                    userTodoData={userTodoData}
                    setUserTodoData={setUserTodoData}
                  />
                ))}
              </Grid.Row>
              <Grid.Row className='eachRow'>
                <Grid.Column className='contentColumn'>
                  <div>
                    <Image
                      src='./images/loa_icons/argos.png'
                      avatar
                      className='contentImage'
                    />
                  </div>
                </Grid.Column>
                {userTodoData.map((item, idx) => (
                  <ArgosRaid
                    argosRaidItem={item}
                    userTodoData={userTodoData}
                    setUserTodoData={setUserTodoData}
                  />
                ))}
              </Grid.Row>
              <Grid.Row className='eachRow baltanRow'>
                <Grid.Column className='contentColumn'>
                  <div>
                    <Image
                      src='./images/loa_icons/baltan.png'
                      avatar
                      className='contentImage'
                    />
                  </div>
                </Grid.Column>
                {userTodoData.map((item, idx) => (
                  <BaltanRaid
                    baltanRaidItem={item}
                    userTodoData={userTodoData}
                    setUserTodoData={setUserTodoData}
                  />
                ))}
              </Grid.Row>
              <Grid.Row className='eachRow'>
                <Grid.Column className='contentColumn'>
                  <div>
                    <Image
                      src='./images/loa_icons/biakiss.png'
                      avatar
                      className='contentImage'
                    />
                  </div>
                </Grid.Column>
                {userTodoData.map((item, idx) => (
                  <BiakissRaid
                    biakissRaidItem={item}
                    userTodoData={userTodoData}
                    setUserTodoData={setUserTodoData}
                  />
                ))}
              </Grid.Row>
              <Grid.Row className='eachRow'>
                <Grid.Column className='contentColumn'>
                  <div>
                    <Image
                      src='./images/loa_icons/kukuseitn.png'
                      avatar
                      className='contentImage'
                    />
                  </div>
                </Grid.Column>
                {userTodoData.map((item, idx) => (
                  <KukseitnRaid
                    kukseitnRaidItem={item}
                    userTodoData={userTodoData}
                    setUserTodoData={setUserTodoData}
                  />
                ))}
              </Grid.Row>
              <Grid.Row className='abrelRow'>
                <Grid.Column className='contentColumn'>
                  <div>
                    <Image
                      src='./images/loa_icons/abrel.png'
                      avatar
                      className='contentImage'
                    />
                  </div>
                </Grid.Column>
                {userTodoData.map((item, idx) => (
                  <AbrelRaid
                    abrelRaidItem={item}
                    userTodoData={userTodoData}
                    setUserTodoData={setUserTodoData}
                  />
                ))}
              </Grid.Row>
            </Grid>
          </Segment>
        </Segment>
      )}

      {addCharacterModal && (
        <AddCharacter
          addCharacterModal={addCharacterModal}
          closeAddCharacter={closeAddCharacter}
          userTodoData={userTodoData}
          axiosConfigAuth={axiosConfigAuth}
          viewPage={viewPage}
          limit={limit}
        />
      )}
      <ToastContainer autoClose={3000} />
    </>
  );
}

export default CharacterToDoRowMobile;
